<template>
  <div>
    <Row>
      <Col span="12">
        <span>邮箱</span>
        <Input style="width:200px;" v-model="userEmail"></Input>
      </Col>
      <Col span="12" style="text-align:right;">
        <Button type="primary" @click="getList">搜索</Button>
      </Col>
    </Row>
    <br />
    <Row>
      <Table border :columns="columns" :data="list"></Table>
    </Row>
    <br />
    <Row>
      <Col span="24" style="text-align:right;">
        <Page
          :total="total"
          show-total
          show-sizer
          show-elevator
          :loading="loading"
          :current="page"
          :page-size="pageSize"
          @on-change="changePage"
          @on-page-size-change="changePageSize"
        />
      </Col>
    </Row>
  </div>
</template>
<script>
export default {
  name: "userManagement",
  data() {
    let _this = this;
    return {
      loading:false,
      columns: [
        {
          title: "序号",
          align: "center",
          render: (h, params) => {
            return h(
              "span",
              params.index + (this.page - 1) * this.pageSize + 1
            );
          }
        },
        {
          title: "昵称",
          key: "userName",
          align: "center"
        },
        {
          title: "注册邮箱",
          key: "userEmail",
          align: "center"
        },
        {
          title: "首次使用时间",
          key: "createTime",
          align: "center"
        },
        {
          title: "操作",
          align: "center",
          render: function(h, param) {
            return h(
              "a",
              {
                style: {
                  color: "#00C1DE"
                },
                on: {
                  click: () => {
                    _this.$router.push({
                      path: "/userdetail",
                      query: {
                        id: param.row.id
                      }
                    });
                  }
                }
              },
              "查看详情"
            );
          }
        }
      ],
      list: [],
      page: 1,
      pageSize: 10,
      userEmail: "",
      total: 0
    };
  },
  methods: {
    changePage(page) {
      this.page = page;
    },
    changePageSize(pageSize) {
      this.pageSize = pageSize;
    },
    getList() {
      this.loading = true;
      let obj = {
        page: this.page,
        pageSize: this.pageSize
      };
      if (this.userEmail) {
        obj.userEmail = this.userEmail;
      }
      this.$axios.post("api/user/getUserList", obj).then(res => {
        this.loading = false;
        this.list = res.data.data.records;
        this.total = res.data.data.total;
      });
    }
  },
  mounted() {
    this.getList();
  }
};
</script>
<style lang="">
</style>


